.film-container {
  padding-top: 20px;
  padding-bottom: 40px;
  @include tablet {
    padding-top: 30px;
    padding-bottom: 50px;
  }
}
.types-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
  @include tablet {
    padding-bottom: 30px;
  }
}
.type-title{
  margin-right: 20px;
}
.type-button{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 30px;
    font-weight: 500;
    line-height: 1.3;
    text-transform: uppercase;
    color: var(--main-text-color);
    background: transparent;
    border-radius: 5px;
    border: 1px solid var(--main-text-color);
    transition: background-color 250ms linear;
    &.movie {
      margin-right: 14px;
    }
    &.selected{
      cursor:default;
      outline:none;
      transition: background-color 250ms linear;
      border: none;
      background-color: var(--accent-color);
      color: var(--white-text);
    }
    &:active {
      outline:none;
      transition: background-color 250ms linear;
      border: none;
      background-color: var(--accent-color);
      color: var(--white-text);
    }
    @include tablet {
      width: 125px;
      padding-right: 10px;
      padding-left: 10px;
      &:hover,&:focus {
      outline:none;
        transition: background-color 250ms linear;
        border: none;
        background-color: var(--accent-color);
        color: var(--white-text);
      }
      &.selected:hover,&.selected:focus {
        cursor:default;
        outline:none;
        transition: background-color 250ms linear;
        border: none;
        background-color: var(--accent-color);
        color: var(--white-text);
      }
    }
    @include desktop {
      width: 136px;
    }
}
.nothing-added {
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.3;
  color: #8c8c8c;
}
.film-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: -20px;
  @include tablet {
    margin-bottom: -30px;
    margin-right: -30px;
  }
}
.movie-card {
  cursor: pointer;
  margin-bottom: 20px;
  border-radius: 5px;
  &:active {
    transition: transform 250ms linear;
    transform: scale(1.03);
  }
  &:active .movie-poster {
    transition: box-shadow 250ms linear;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.8);
  }
  @include tablet {
    &:hover {
      transition: transform 250ms linear;
      transform: scale(1.03);
    }
    &:hover .movie-poster {
      transition: box-shadow 250ms linear;
      box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.8);
    }
    flex-basis: calc(100% / 2 - 30px);
    margin-bottom: 30px;
    margin-right: 30px;
  }
  @include desktop {
    flex-basis: calc(100% / 3 - 30px);
  }
}
.movie-poster {
  height: auto;
  width: 100%;
  border-radius: 5px;
  margin-bottom: 10px;
}
.movie-name {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.3;
}
.movie-thumb {
  margin-top: 2px;
  color: var(--accent-color);
  font-weight: 500;
  font-size: 12px;
  line-height: 1;
}
.movie-genres {
  display: inline-block;
  padding-right: 5px;
  border-right: 1.5px solid var(--accent-color);
}
.movie-date {
  display: inline-block;
}
.movie-rate {
  display: inline-block;
  margin-left: 11px;
  padding: 1px 10px;
  border-radius: 5px;
  color: var(--white-text);
  background-color: var(--accent-color);
}
