:root {
  --accent-color: #ff6b08;
  --error-color: #ff001b;
  --footer-text-color: #545454;
  --white-text: #fff;
  --main-text-color: #000;
}

body {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
  position: relative;
}

main {
  flex-grow: 1;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media (width >= 768px) {
  .container {
    max-width: 768px;
    padding-left: 76px;
    padding-right: 76px;
  }
}

@media (width >= 1024px) {
  .container {
    max-width: 1024px;
    padding-left: 71px;
    padding-right: 71px;
  }
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-family: Roboto, sans-serif;
}

button {
  padding: 0;
  font-family: Roboto, sans-serif;
}

p {
  margin: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

html {
  min-height: 100%;
  position: relative;
}

.visually-hidden {
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  border: 0 !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  position: absolute !important;
}

.container.nav {
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  padding-top: 41px;
  display: flex;
}

.filmoteka {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.filmoteka .title {
  color: var(--white-text);
  font-family: Roboto, sans-serif;
  font-size: 30px;
  font-weight: 500;
  display: none;
}

@media (width >= 768px) {
  .filmoteka .title {
    display: inline;
  }
}

.filmIcon {
  width: 24px;
  height: 24px;
}

@media (width >= 768px) {
  .filmIcon {
    margin-right: 14px;
  }
}

.nav__buttons {
  flex-direction: row;
  display: flex;
}

.nav_button {
  text-transform: uppercase;
  cursor: pointer;
  color: var(--white-text);
  background: none;
  border: none;
  border-bottom: 3px solid #0000;
  flex-direction: row;
  justify-content: center;
  height: -moz-fit-content;
  height: fit-content;
  font-size: 12px;
  line-height: 1.2em;
  transition: border .25s linear;
  display: flex;
}

.nav_button.current {
  cursor: default;
  border-bottom: 3px solid var(--accent-color);
  transition: border .25s linear;
}

.nav_button:active {
  border-bottom: 3px solid var(--accent-color);
  outline: none;
  transition: border .25s linear;
}

.nav_button:first-child {
  margin-right: 39px;
}

@media (width >= 768px) {
  .nav_button:hover, .nav_button:focus {
    border-bottom: 3px solid var(--accent-color);
    outline: none;
    transition: border .25s linear;
  }
}

