  background-image: linear-gradient(#0000008f, #0000008f), -webkit-image-set(url("library.b1d4c3e5.jpg") 1x, url("library@2x.3bc30225.jpg") 2x);
  background-position: top;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

@media (width >= 768px) {
  .library-page {
    background-image: -webkit-linear-gradient(#0000008f, #0000008f), -webkit-image-set(url("library.30b604c7.jpg") 1x, url("library@2x.0c2d9915.jpg") 2x);
    background-image: linear-gradient(#0000008f, #0000008f), -webkit-image-set(url("library.30b604c7.jpg") 1x, url("library@2x.0c2d9915.jpg") 2x);
    background-size: cover;
  }
}

@media (width >= 1024px) {
  .library-page {
    background-image: -webkit-linear-gradient(#0000008f, #0000008f), -webkit-image-set(url("library.39ad9a6a.jpg") 1x, url("library@2x.12c70744.jpg") 2x);
