.header-features {
  flex-direction: row;
  justify-content: center;
  padding-top: 38px;
  padding-bottom: 90px;
  display: flex;
}

.header-features.library {
  padding-bottom: 73px;
}

.form-box {
  flex-direction: column;
  align-items: center;
  width: 100%;
  display: flex;
  position: relative;
}

.search-form {
  width: 100%;
  max-width: 280px;
  position: relative;
}

.search-form:hover .search-input {
  border-bottom-color: var(--accent-color);
  transition: border-bottom-color .25s linear;
}

.search-form:hover .search-icon use.svg {
  stroke: var(--accent-color);
  transition: stroke .25s linear;
}

@media (width >= 768px) {
  .search-form {
    max-width: 336px;
  }
}

.search-input {
  color: var(--white-text);
  background: none;
  border: none;
  border-bottom: .5px solid #fff;
  outline: none;
  width: 100%;
  padding-bottom: 4px;
  padding-right: 15px;
  line-height: 1;
  transition: border-bottom-color .25s linear;
}

.search-input:focus {
  border-bottom-color: var(--accent-color);
  transition: border-bottom-color .25s linear;
}

.search-input:focus + .search-icon use.svg {
  stroke: var(--accent-color);
  transition: stroke .25s linear;
}

.search-input:focus::placeholder {
  color: #0000;
  transition: color .25s linear;
}

.search-input::placeholder {
  color: var(--white-text);
  transition: color .25s linear;
}

.search-icon {
  width: 12px;
  height: 12px;
  position: absolute;
  bottom: 7px;
  right: 0;
}

.search-icon path {
  fill: inherit;
  stroke: inherit;
}

.search-icon .svg {
  fill: none;
  stroke: var(--white-text);
  transition: stroke .25s linear;
}

.error {
  color: var(--error-color);
  width: 100%;
  font-size: 14px;
  line-height: 1.2;
  position: absolute;
  top: 44px;
}

@media (width >= 768px) {
  .error {
    width: unset;
  }
}

.library-buttons {
  flex-direction: row;
  display: flex;
}

.library-button {
  cursor: pointer;
  text-transform: uppercase;
  color: var(--white-text);
  border: 1px solid var(--white-text);
  background: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 130px;
  height: 44px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.3;
  transition: background-color .25s linear;
  display: flex;
}

.library-button.current {
  cursor: default;
  background-color: var(--accent-color);
  border: none;
  transition: background-color .25s linear;
}

.library-button:active {
  background-color: var(--accent-color);
  border: none;
  outline: none;
  transition: background-color .25s linear;
}

.library-button:first-child {
  margin-right: 15px;
}

@media (width >= 768px) {
  .library-button {
    width: 136px;
  }

  .library-button:hover, .library-button:focus {
    background-color: var(--accent-color);
    border: none;
    outline: none;
    transition: background-color .25s linear;
  }
}

.loader {
  width: 80px;
  height: 100px;
  position: fixed;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
}

.loader-backdrop {
  background-color: #ffffff80;
  width: 100%;
  height: 100%;
  position: absolute;
}

.block {
  box-sizing: border-box;
  float: left;
  background: var(--accent-color);
  border-radius: 3px;
  width: 12px;
  height: 12px;
  margin: 0 10px 10px 0;
  position: relative;
}

.block:nth-child(4n+1) {
  animation: 2s infinite wave;
}

.block:nth-child(4n+2) {
  animation: 2s .2s infinite wave;
}

.block:nth-child(4n+3) {
  animation: 2s .4s infinite wave;
}

.block:nth-child(4n+4) {
  margin-right: 0;
  animation: 2s .6s infinite wave;
}

@keyframes wave {
  0% {
    opacity: 1;
    top: 0;
  }

  50% {
    opacity: .2;
    top: 30px;
  }

  100% {
    opacity: 1;
    top: 0;
  }
}

