    width: 125px;
    padding-left: 10px;
    padding-right: 10px;
  }

  .modal-button:hover, .modal-button:focus {
    background-color: var(--accent-color);
    color: var(--white-text);
    border: none;
    outline: none;
    transition: background-color .25s linear;
  }

  .modal-button.selected:hover, .modal-button.selected:focus {
    border: 1px solid var(--main-text-color);
    color: var(--main-text-color);
    background: none;
    outline: none;
    transition: background-color .25s linear;
  }
}

@media (width >= 1024px) {
  .modal-button {
    width: 136px;
  }
}

